<template>
    <div class="featured">
        <title-view
            :label="titleText"
            :btn="btnText"
            v-if="titleVisible && data && data.length > 0"
        ></title-view>
        <div class="featured-list">
            <div class="featured-item-wrapper">
                <div
                    class="featured-item"
                    v-for="(item, index) in data"
                    :key="index"
                    @click="showBookDetail(item)"
                >
                    <div class="img-wrapper">
                        <img class="img" v-lazy="item.cover" />
                    </div>
                    <div class="content-wrapper">
                        <div class="title title-small" :ref="setTitleRefs">
                            {{ item.title }}
                        </div>
                        <div class="author sub-title-tiny" :ref="setAuthorRefs">
                            {{ item.author }}
                        </div>
                        <div
                            class="category third-title-tiny"
                            :ref="setCategoryRefs"
                        >
                            {{ categoryText(item.category) }}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import TitleView from '@/components/home/title'
import { realPx } from '@/utils/utils'
import { categoryText } from '@/utils/store'
import { storeHomeMixin } from '@/utils/mixin'
export default {
    mixins: [storeHomeMixin],
    components: {
        TitleView
    },
    props: {
        data: Array,
        titleVisible: {
            type: Boolean,
            default: true
        },
        titleText: {
            type: String
        },
        btnText: {
            type: String
        }
    },
    data() {
        return {
            titleRefs: [],
            authorRefs: [],
            categoryRefs: []
        }
    },
    computed: {
        width() {
            return window.innerWidth - realPx(20) - realPx(60) + 'px'
        }
    },
    methods: {
        setTitleRefs(el) {
            this.titleRefs.push(el)
        },
        setAuthorRefs(el) {
            this.authorRefs.push(el)
        },
        setCategoryRefs(el) {
            this.categoryRefs.push(el)
        },
        categoryText(category) {
            return categoryText(category, this)
        },
        // 并没有用到？
        resize() {
            this.$nextTick(() => {
                for (const index in this.titleRefs) {
                    this.titleRefs[index].style.width = this.width
                    this.authorRefs[index].style.width = this.width
                    this.categoryRefs[index].style.width = this.width
                }
            })
        }
    },
    beforeUpdate() {
        this.titleRefs = []
        this.authorRefs = []
        this.categoryRefs = []
    }
}
</script>
<style lang="scss" scoped>
@import '@/assets/styles/global';
.featured {
    .featured-list {
        width: 100%;
        padding: 0 px2rem(10);
        box-sizing: border-box;
        .featured-item-wrapper {
            width: 100%;
            display: flex;
            flex-flow: row wrap;
            justify-content: flex-start;
            align-items: flex-start;
            .featured-item {
                flex: 0 0 50%;
                width: 50%;
                padding: px2rem(5) px2rem(5);
                box-sizing: border-box;
                @include top;
                .img-wrapper {
                    flex: 0 0 30%;
                    width: 30%;
                    .img {
                        width: 100%;
                    }
                }
                .content-wrapper {
                    flex: 1;
                    width: px2rem(117.5);
                    padding: 0 px2rem(5);
                    box-sizing: border-box;
                    .author {
                        margin-top: px2rem(15);
                    }
                    .category {
                        margin-top: px2rem(5);
                    }
                }
            }
        }
    }
}
</style>
